padding: 20px;\n  display: inline-block;\n}\n\n.header_title {\n  font-weight: 700;\n  font-size: 3em;\n  margin: 0.5em 0.25em;\n  text-align: center;\n  text-transform: uppercase;\n}\n\n.header_subtitle {\n  font-weight: bold;\n  font-size: 20px;\n  font-family: \"Lato\", sans-serif;\n  font-style: italic;\n  text-align: center;\n}\n\n.header_menu {\n  font-size: 35px;\n  margin: 10px auto;\n  text-align: center;\n  display: block;\n  text-decoration: none;\n  color: rgb(121, 88, 5);\n  min-height: 45px;\n  min-width: 45px;\n  border: none;\n}\n\n// nav\n\n.nav {\n  width: 100%;\n  margin: 0 auto;\n  text-align: center;\n  background-color: wheat;\n}\n\n.nav_list {\n  width: 100%;\n}\n\n.nav_item {\n  box-sizing: border-box;\n  display: inline-block;\n  width: 24%;\n  text-align: center;\n  line-height: 24px;\n  text-transform: uppercase;\n}\n\n.nav a {\n  text-decoration: none;\n  color: rgb(107, 87, 53);\n  padding: 1em;\n  display: inline-block;\n}\n\n.nav a:hover {\n  color: rgb(109, 57, 9);\n  text-decoration: underline;\n  font-weight: bold;\n}\n\n// jumbotron\n\n.hero {\n  display: flex;\n  width: 100%;\n  align-items: center;\n  margin-bottom: 15px;\n}\n\n.hero_image {\n  margin: 0 auto;\n  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;\n}\n\n// main\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n// content\n.content {\n  padding: 32px;\n}\n\n.caption {\n  text-align: center;\n  font-size: 30px;\n}\n\n//resto\n.restaurants {\n  margin: 32px auto auto;\n  text-align: left;\n}\n\n//daftar resto\n\n.restaurant-list {\n  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;\n  width: 100%;\n  border-radius: 10px;\n  overflow: hidden;\n  line-height: 0.75cm;\n  background-color: cornsilk;  \n  padding: 16px;\n}\n\n.restaurant-list_header {\n  position: relative;\n}\n\n.restaurant-list_header_picture {\n  width: 100%;\n}\n\n.restaurant-list .restaurant-list_header .restaurant-list_header_rating {\n  position: relative;\n  display: inline-block;\n  padding: 2px 4px;\n  bottom: 50px;\n  right: 10px;\n  border-radius: 0 10px 10px 0;\n  background-color: rgba(112, 64, 2, 0.75);\n  font-weight: bold;\n  font-size: 18px;\n  color: cornsilk;\n  margin-left: 10px;\n}\n\n.restaurant-list_description h2 {\n  font-size: 20px;\n}\n\n.restaurant-list_description a {\n  text-decoration: none;\n  padding: 1em 0;\n  color: rgb(68, 55, 46);\n}\n\n.restaurant-list_description a:hover {\n  text-decoration: underline;\n}\n\n.restaurant-list_description p {\n  font-size: 16px;\n  margin-top: 0.75em;\n  color: rgb(121, 51, 5);\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 4;\n  -webkit-box-orient: vertical;\n}\n\n// query\n\n#query {\n  display: block;\n  width: 100%;\n  min-height: 45px;\n  margin-bottom: 2em;\n  margin-left: auto;\n  margin-right: auto;\n  border: 2.5px solid dimgray;\n  border-radius: 15px;\n  text-align: center;\n}\n\n// resto detail\n\n.restaurant-name {\n  font-size: 35px;\n  color: rgb(1, 87, 87);\n  text-align: center;\n  text-decoration: underline;\n}\n\n.restaurant-picture {\n  margin: 30px auto 10px;\n  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;  \n  border-radius: 15px;\n}\n\n.detail_restaurant {\n  margin: 0 40px 0;\n  display: grid;\n  grid-row-gap: 20px;\n}\n\n.detail_restaurant h2,\n#reviewResto h2 {\n  font-size: 25px;\n  margin: 15px auto;\n  color: darkolivegreen;\n  text-align: center;\n}\n\n.detail_restaurant h3 {\n  font-size: 20px;\n  margin: 8px auto;\n  color: saddlebrown;\n}\n\n.detail_restaurant p {\n  font-size: 18px;\n  font-weight: normal;\n  color: rgb(48, 32, 21);\n  line-height: 0.75cm;\n}\n\n.restaurant-info, \n.restaurant-menu, \n.restaurant-description {\n  grid-template-rows: 1fr;\n  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;  \n  background-color: cornsilk;\n  text-align: center;\n  padding: 10px 50px 25px;\n  border-radius: 10px;\n  width: 100%;\n}\n\n.restaurant-menu p {\n  text-transform: capitalize;\n}\n\n// restaurant-reviews\n\n#reviewResto {\n  margin: 0 40px 32px;\n  padding: 16px 80px;\n}\n\n.restaurant-reviews {\n  display: grid;\n  grid-gap: 20px;\n}\n\n.review-custom {\n  display: grid;\n  margin: 20px 0;\n}\n\n.user-review, \n.customer-review {\n  box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;  \n  background-color: cornsilk;\n  border-radius: 10px;\n  line-height: 0.75cm;\n}\n\n.user-review .caption-review {\n  color: rgb(1, 87, 87);\n  font-weight: bold;\n  font-size: 20px;\n}\n\n#userName,\n#userReview {\n  line-height: 44px;\n  width: 100%;\n  border-radius: 10px;\n  border: 2px solid tan;\n}\n\n#submitButton {\n  min-height: 45px;\n  width: 110px;\n  text-decoration: none;\n  background-color: rgb(1, 87, 87);\n  color: cornsilk;\n  font-size: 18px;\n  border: 0;\n  border-radius: 25px;\n  margin-bottom: 15px;\n}\n\n.customer-review {\n  padding: 20px 32px;\n}\n\n.customer-review b {\n  color: olive;\n  font-size: 20px;\n}\n\n.customer-review p {\n  font-size: 18px;\n}\n\n// like button\n\n.like {\n  width: 60px;\n  height: 60px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: sienna;\n  \n  position: fixed;\n  bottom: 30px;\n  right: 20px;\n  border-radius: 50%;\n  border: 0;\n  \n  font-size: 25px;\n  color: blanchedalmond;\n  cursor: pointer;\n}\n\n// footer\n\nfooter {\n  background-color: burlywood;\n  padding: 1.25em;\n  width: 100%;\n}\n\nfooter p {\n  text-align: center;\n  font-weight: bolder;\n  font-family: \"Lato\", sans-serif;\n  font-size: 16px;\n  color:rgb(80, 38, 9);\n}"],"sourceRoot":""}]);
